<template>
  <div class="img-page">
    <fixed-header title="景区图片"></fixed-header>
    <ul class="img-list">
      <li class="img-item" v-for="(item, index) in this.imgs" :key="index" @click="showGallary(index)">
        <img :src="item">
      </li>
    </ul>
    <!-- 弹出层 图片单张展示 -->
    <v-fade>
      <v-gallary :gallaryImgs="this.imgs" ref="gallary">{{ this.$route.params.id }}</v-gallary>
    </v-fade>
  </div>
</template>

<script>
import VGallary from '@/components/gallary/Gallary.vue'
import VFade from '@/components/animation/Fade.vue'
import FixedHeader from '@/components/fixed-header/FixedHeader.vue'

import axios from 'axios'

export default {
  name: 'SightImgs',
  components: {
    VGallary,
    VFade,
    FixedHeader
  },
  data () {
    return {
      imgs: [],
      oldId: []
    }
  },
  mounted () {
    this.getImgs()
    this.oldId.push(this.$route.params.id)
    console.log('mounted', this.oldId, this.$route.params.id)
  },
  activated () {
    const newId = this.$route.params.id
    if (this.oldId.indexOf(newId) === -1) {
      this.getImgs()
      this.oldId.push(newId)
    }
    console.log('updated', this.oldId, this.$route.params.id)
  },
  methods: {
    showGallary (index) {
      this.$refs.gallary.show(index)
    },
    getImgs () {
      axios.get('/api/sightImgs.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleDataSucc)
    },
    handleDataSucc (res) {
      this.imgs = res.data.data.imgs
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~assets/styles/varibles.styl'

.img-page
  position absolute
  top 0
  left 0
  width 100%
  min-height 100%
  background-color #f5f5f5

.img-list
  padding $fh-height+0.2rem .2rem .4rem .2rem
  overflow hidden
  .img-item
    width 50%
    float left
    margin-bottom 0.1rem
    box-sizing border-box
    &:nth-child(2n+1)
      padding-right 0.05rem
    &:nth-child(2n)
      padding-left 0.05rem
    img
      width 100%
</style>



