<template>
  <div class="gallary" v-show="isShow" @click="close">
    <div class="wrapper">
      <swiper :options="swiperOption" ref="mySwiper">
        <swiper-slide v-for="(item, index) in gallaryImgs" :key="index">
          <img class="swiper-img" :src="item" />
        </swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    gallaryImgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        loop: true,
        observer: true,
        observeParents: true,
        initialSlide: 0
      },
      isShow: false
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  methods: {
    close () {
      this.isShow = false
    },
    show (index) {
      if (index || index === 0) {
        // 指定显示第几个slide
        this.swiper.slideTo(index + 1, 0)
      }
      this.isShow = true
    }
  }
}
</script>

<style lang="stylus" scoped>
  .wrapper >>> .swiper-container
    overflow inherit
  .gallary
    display flex
    flex-direction column
    justify-content center
    position fixed
    top 0
    bottom 0
    left 0
    right 0
    background-color #000
    z-index 99
    color #fff
    .wrapper
      width 100%
      height 0
      padding-bottom 100%
      .swiper-pagination
        bottom -1rem
        color #fff

  .swiper-img
    width 100%
</style>



